body {
  margin: 0;
  box-sizing: border-box;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white; }

.navbar {
  background-color: #ECB22E;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  /* bottom:0 */
  height: 60px;
  width: 100%;
  z-index: 1; }
  .navbar .nav-content {
    display: flex;
    align-items: center; }
    .navbar .nav-content .nav-img {
      height: 40px;
      /* border: solid 2px gray */
      border-radius: 50%;
      background-color: #fff; }
    .navbar .nav-content .nav-title {
      margin-left: 10px;
      font-family: 'Roboto', sans-serif;
      font-size: 30px;
      color: white; }

/* The Modal (background) */
.modal {
  display: flex;
  /* block by default */
  justify-content: center;
  align-items: center;
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */ }

/* Modal Content */
.modal-content {
  background-color: #fff;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 70vh;
  height: 70vh;
  border-radius: 2vh; }

/* The Close Button */
.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold; }

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer; }

.chess {
  margin-left: 15px;
  margin-right: 15px; }

.headbar {
  display: flex;
  justify-content: space-between;
  margin: 0 15px 15px 15px; }

#app {
  /* width:40rem */
  /* height: 50vh */
  border: solid 3px black;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 1fr);
  width: 70vh;
  height: 70vh; }

.chess-box {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden; }
  .chess-box .chess-piece {
    transition: 1s;
    width: 80%;
    height: auto; }
  .chess-box.selected {
    box-shadow: inset 0 0 30px #311B0B; }
  .chess-box.possible-move {
    box-shadow: inset 0 0 30px #0f0; }
  .chess-box.possible-target {
    box-shadow: inset 0 0 30px yellow; }
  .chess-box.checker {
    box-shadow: inset 0 0 30px blue; }
  .chess-box.checked-white {
    box-shadow: inset 0 0 30px red; }
  .chess-box.checked-black {
    box-shadow: inset 0 0 30px red; }
  .chess-box.castling {
    box-shadow: inset 0 0 30px #0f0; }

.white {
  background-color: #fff;
  background-position: center;
  background-size: cover;
  /* background-image: url('../../../assets/white-marble.jpg') */
  filter: brightness(1.1); }

.black {
  background-color: pink;
  background-position: center;
  background-size: cover;
  /* background-image: url('../../../assets/white-marble.jpg') */
  filter: brightness(1.1);
  /* background-image: url('../../../assets/green-marble.jpg') */
  /* background-image: url('../../../assets/dark-wood.jpg') */ }

